import React, { useState, useEffect, useRef } from "react";
import { Box, Text, Heading, Input, Button } from "rimble-ui";
import NoMsg from "../../assets/nochat.png";
import Message from "./Message";
import UserInfo from "./UserInfo";
function ChatPage({ curr, notifier }) {
    const [conn, setConn] = useState(0);
    const ref = useRef();
    const [renderMsg, setRenderMsg] = useState([]);
    function sendMessage() {
        const item = JSON.parse(localStorage.getItem(curr.address || curr));
        const message = ref.current.value;
        const pk = item.data.message.publicKey;
        const sk = window.user.userInfo.secretKey;
        window.peer.broadCast(window.encrypt(pk, sk, message, curr), "BLOCK");
        ref.current.value = "";
        setRenderMsg([
            ...renderMsg,
            <Message
                message={message}
                time={`${new Date().getHours()}:${new Date().getMinutes()}`}
                sent={true}
                key={message}
            />,
        ]);
    }
    useEffect(() => {
        window.peer.numConnections.subscribe((e) => setConn(e));
        setRenderMsg([]);
        // console.log(renderMsg);
        window.notifier?.subscribe((e) => {
            e.forEach((el) => {
                if (
                    el.data.to === window.user.keyHash ||
                    el.data.to?.address === window.user.keyHash
                ) {
                    const message = window.decrypt(
                        JSON.parse(localStorage.getItem(el.data.from)).data
                            .message.publicKey,
                        window.user.userInfo.secretKey,
                        el.data
                    );
                    setRenderMsg([
                        ...renderMsg,
                        <Message
                            message={message.message}
                            sent={false}
                            time={`${new Date(
                                message.timestamp
                            ).getHours()}:${new Date(
                                message.timestamp
                            ).getMinutes()}`}
                        />,
                    ]);
                }
            });
        });
    }, [curr]);

    return (
        <div className="hideChat">
            <Box style={{ height: "100%" }} color="#ffffff">
                {!curr && (
                    <React.Fragment>
                        <div
                            style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                height: "inherit",
                            }}
                        >
                            <span
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                    justifyContent: "center",
                                    flexDirection: "column",
                                    gap: "0.5rem",
                                }}
                            >
                                <img src={NoMsg} width="200" />
                                <Heading.h4 my={1}>
                                    It's always nice to chat with to someone
                                </Heading.h4>
                                <Text my={0}>
                                    Pick a chat from the left menu, or add your
                                    friends to start a new chat.
                                </Text>
                                <Text my={1}>
                                    By the way you're currently directly
                                    connected to <b>{conn}</b> users.
                                </Text>
                                <small
                                    style={{
                                        marginTop: "2rem",
                                        width: "60%",
                                    }}
                                >
                                    All your messages are always end-to-end
                                    encrypted to never go to any server.
                                    Everything happens on a blockchain powered
                                    by people like you. <wbr /> Meschain is
                                    built on top of blockchain where all the
                                    users compete to add your new message to the
                                    blockchain. Note that once you've sent a
                                    message it is for forever no one (not even
                                    you) can alter or delete it.
                                </small>
                            </span>
                        </div>
                    </React.Fragment>
                )}
                {curr && (
                    <React.Fragment>
                        <Box
                            bg={"#4a4a4a"}
                            p={2}
                            style={{
                                display: "flex",
                                alignItems: "center",
                            }}
                        >
                            <UserInfo addr={curr} />
                        </Box>
                        <div>
                            <div class="card-body msg_card_body">
                                <div
                                    style={{
                                        display: "flex",
                                        "justify-content": "center",
                                        transform: "scale(0.8)",
                                    }}
                                >
                                    {/* <Button
                                        size="small"
                                        mr={3}
                                        style={{
                                            "background-color": "a7a7ad",
                                        }}
                                    >
                                        Today
                                    </Button> */}
                                </div>
                                {renderMsg.map((el) => el)}
                            </div>
                            <div
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                }}
                            >
                                <Button.Outline
                                    icon="Attachment"
                                    icononly
                                    style={{
                                        borderBottomRightRadius: 0,
                                        borderTopRightRadius: 0,
                                        backgroundColor: "#ffffff",
                                        marginRight: 0,
                                        marginLeft: 25,
                                    }}
                                ></Button.Outline>
                                <Input
                                    type="text"
                                    required={true}
                                    placeholder="Type your message..."
                                    ref={ref}
                                    style={{
                                        width: "60vw",
                                        margin: "5px 0px",
                                        "border-radius": 0,
                                    }}
                                />
                                <Button
                                    icon="Send"
                                    icononly
                                    onClick={sendMessage}
                                    style={{
                                        marginRight: "30px",
                                        borderTopLeftRadius: 0,
                                        borderBottomLeftRadius: 0,
                                    }}
                                />
                            </div>
                        </div>
                    </React.Fragment>
                )}
            </Box>
        </div>
    );
}

export default ChatPage;
