import React from "react";

function Logs({ label, type, text }) {
    return (
        <div
            style={{
                fontSize: "0.8rem",
                background: "#4b4b4b",
                marginBottom: "0.3rem",
                padding: "0.6rem",
                display: "flex",
                alignItems: "center",
            }}
        >
            <span
                style={{
                    backgroundColor: `${label}`,
                    borderRadius: "0.4rem",
                    fontSize: "0.85rem",
                    fontWeight: 600,
                    wordBreak: "keep-all",
                    margin: "0 0.2rem",
                    padding: "0.2rem 0.3rem",
                    display: "flex",
                    alignItems: "center",
                }}
            >
                {type}
            </span>
            <span>{text}</span>
        </div>
    );
}

export default Logs;
