import React, { useRef } from "react";

function Message({ message, time, sent }) {
    const ref = useRef();
    const handleTime = () => {
        if (ref.current.children[0].children[0].classList.contains("d-none"))
            ref.current.style.marginBottom = "15px";
        else ref.current.style.marginBottom = "4px";
        ref.current.children[0].children[0].classList.toggle("d-none");
    };
    return (
        <div
            className={
                sent
                    ? "d-flex justify-content-end anim"
                    : "d-flex justify-content-start anim"
            }
            style={{
                marginBottom: "4px",
            }}
            ref={ref}
            onClick={handleTime}
        >
            <div className={sent ? "msg_cotainer" : "msg_cotainer_send"}>
                {message}
                <span
                    className={
                        sent ? "msg_time d-none" : "msg_time_send d-none"
                    }
                >
                    {time}
                </span>
            </div>
        </div>
    );
}

export default Message;
