import React, { useState, useEffect } from "react";
import ChatHistory from "./chatHistory";
import ChatPage from "./ChatPage";
import LogsMenu from "./LogsMenu";

function HomePage() {
    const [currChat, setCurrChat] = useState(undefined);
    const [updateEvent, newUpdateEvent] = useState();
    useEffect(() => {
        window.notifier.subscribe((el) => newUpdateEvent(el));
    }, []);
    return (
        <div className="home">
            <div className="chats">
                <ChatHistory set={setCurrChat} notifier={updateEvent} />
                <ChatPage curr={currChat} notifier={updateEvent} />
            </div>
            <LogsMenu />
        </div>
    );
}

export default HomePage;
