import React, { useEffect, useState } from "react";
import {
    Blockie,
    Box,
    Button,
    EthAddress,
    Field,
    Form,
    Heading,
    Input,
    Text,
    ToastMessage,
} from "rimble-ui";
import ChatHistoryItem from "./ChatHistoryItem";

function ChatHistory({ set, notifier }) {
    const randomColor = () =>
        `#${Math.floor(Math.random() * 16777215).toString(16)}`;
    const [toggle, setToggle] = useState(false);
    const [friend, setFriend] = useState("");
    const [ListChats, setListChats] = useState([]);
    useEffect(() => {
        window.peer.identity.subscribe((e) => {
            try {
                localStorage.setItem(JSON.parse(e).from, e);
                // window.currentChat = JSON.parse(e).data.message.username;
                set(JSON.parse(e).from);
            } catch {}
        });
        setListChats([]);

        notifier?.forEach((el) => {
            if (
                (el.data.to === window.user.keyHash ||
                    el.data.to?.address === window.user.keyHash ||
                    el.data.from === window.user.keyHash) &&
                !ListChats.find((o) => o.props.address === el.data.from)
            ) {
                // If message is addresed to me then create a new chat history
                setListChats([
                    ...ListChats,
                    <ChatHistoryItem
                        name={
                            JSON.parse(localStorage.getItem(el?.data.from))
                                ?.data.message.username || el?.data.from
                        }
                        address={el.data.from}
                        lastText="New Message"
                        handler={set}
                        key={el.data.from}
                    />,
                ]);
            }
        });
    }, [notifier]);

    function newFriend(event) {
        event.preventDefault();
        try {
            if (!localStorage.getItem(friend))
                window.peer.privateMSG(undefined, friend, "WHO");
            else {
                set(friend);
            }
        } catch {
            window.toastProvider.addMessage(
                "Couldn't connect to your friend üòå",
                {
                    secondaryMessage:
                        "May be you entered a wrong address or they are offline",
                    colorTheme: "light",
                    variant: "failure",
                }
            );
        }
        setFriend("");
    }
    function hideLogs() {
        setToggle(!toggle);
        if (toggle) {
            document.querySelector(".logs").style = "";
            document.querySelector(".chats").style = "";
            document.querySelector(".chats").children[0].style = "";
            document.querySelector(".chats").children[1].style = "";
        } else {
            document.querySelector(".logs").style = "display: none";
            document.querySelector(".chats").style = "width: 100%";
            document.querySelector(".chats").children[0].style = "width: 35%";
            document.querySelector(".chats").children[1].style = "width: 65%";
        }
    }
    return (
        <div>
            <Box
                bg={"#2f3136"}
                p={2}
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}
            >
                <Blockie
                    style={{ borderRadius: "50%" }}
                    opts={{
                        seed: sessionStorage["whoami"],
                        color: randomColor(),
                        bgcolor: randomColor(),
                        spotcolor: randomColor(),
                    }}
                />

                <Heading.h6 style={{ margin: 0 }}>
                    Chats of {window.user.userInfo.username || "Anonymous"}
                </Heading.h6>
                <span style={{ display: "flex", alignItems: "center" }}>
                    <Button.Text
                        icon="Info"
                        icononly={true}
                        size="small"
                        title="Stats for Nerds"
                        mr={2}
                        className="logsBTN"
                        onClick={hideLogs}
                        style={{ color: "#ffffff" }}
                    />
                    <Button
                        icon="PowerSettingsNew"
                        size="small"
                        iconpos="right"
                        onClick={() => window.location.reload()}
                    >
                        Log Out
                    </Button>
                </span>
            </Box>
            <Box p={2} bg={"#36393f"}>
                <Text fontSize="1">Your Meschain Address</Text>
                <EthAddress
                    address={sessionStorage["whoami"]}
                    className="hideQR"
                />
            </Box>
            <Box p={2} bg={"#2f3136"}>
                <Form className="fullWidth">
                    <Field label="Your Friends Meschain Address">
                        <Input
                            type="text"
                            placeholder="Ask your friend to share their address"
                            style={{ width: "100%" }}
                            required={true}
                            value={friend}
                            onChange={(e) => setFriend(e.target.value)}
                        />
                    </Field>
                    <Button
                        icon="Add"
                        icononly={true}
                        ml={2}
                        onClick={newFriend}
                    ></Button>
                </Form>
            </Box>
            <Box bg={"#46484b"} style={{ height: "100%", overflowY: "auto" }}>
                {ListChats.map((el) => el)}
                <div className="d-flex align-items-center justify-content-center my-5 py-5">
                    <span>
                        Your chats will appear here. Try sending a new message.
                        üìù
                    </span>
                </div>
            </Box>
            <>
                <ToastMessage.Provider
                    ref={(node) => (window.toastProvider = node)}
                />
            </>
        </div>
    );
}

export default ChatHistory;
