import React, { useEffect, useState } from "react";
import { Avatar, Text, Blockie } from "rimble-ui";
function UserInfo({ addr }) {
    const randomColor = () =>
        `#${Math.floor(Math.random() * 16777215).toString(16)}`;
    const [name, setName] = useState();
    useEffect(() => {
        const names = [
            "Panda",
            "Kiwi",
            "Mango",
            "Doge",
            "Dragon",
            "Ninja",
            "Koala",
            "Tiger",
            "Quokka",
            "Bull",
            "Chicken",
        ];
        setName("Anonymous " + names[Math.floor(Math.random() * names.length)]);
    }, [addr.address]);
    return (
        <React.Fragment>
            {addr.blockie && <Avatar src={addr.blockie} size="45px" />}
            {!addr.blockie && (
                <Blockie
                    style={{ borderRadius: "50%" }}
                    opts={{
                        seed: addr.address,
                        color: randomColor(),
                        bgcolor: randomColor(),
                        spotcolor: randomColor(),
                    }}
                />
            )}
            <Text p={2}>
                {JSON.parse(localStorage.getItem(addr.address))?.data.message
                    .username || name}
            </Text>
        </React.Fragment>
    );
}

export default UserInfo;
