import React, { useState } from "react";
import {
    Box,
    Button,
    Card,
    Field,
    Heading,
    Input,
    Link,
    Text,
} from "rimble-ui";
function AuthPage() {
    if (sessionStorage["whoami"]) window.location.hash = "#/";
    function keyUpload(event) {
        const file = event.nativeEvent.srcElement.files[0];
        const reader = new FileReader();
        reader.onload = function (e) {
            sessionStorage.setItem("file", e.target.result);
            sessionStorage.setItem("kind", 0);
            window.location.hash = "#/setup";
        };
        reader.readAsText(file);
    }
    const [value, setValue] = useState("");
    const [page, setPage] = useState(1);
    return (
        <div className="main__class">
            <Card
                width={"auto"}
                mx={"auto"}
                my={"auto"}
                maxWidth={"420px"}
                bg={"#2c2c2c"}
                color={"white"}
                border={0}
                p={4}
                style={{ boxShadow: "9px 9px 8px rgb(0 0 0)" }}
            >
                {page === 0 && (
                    <React.Fragment>
                        <Heading>Log In to Meschain</Heading>
                        <Text.p>
                            If you have your keys with you go ahead and upload
                            them
                        </Text.p>
                        <Box
                            mx={"auto"}
                            style={{
                                display: "flex",
                                justifyContent: "center",
                            }}
                        >
                            <Field label="Your key file">
                                <Input
                                    type="file"
                                    required={true}
                                    accept=".meschain"
                                    placeholder="Keys"
                                    onChange={keyUpload}
                                />
                            </Field>
                        </Box>
                        <Text.p style={{ fontStyle: "italic" }}>
                            Don't have keys? Get new one{" "}
                            <Link
                                title="Click here to signup"
                                onClick={() => setPage(1)}
                            >
                                here.
                            </Link>
                        </Text.p>
                    </React.Fragment>
                )}
                {page === 1 && (
                    <React.Fragment>
                        <Heading>SignUp for Meschain</Heading>
                        <Text.p>
                            No need for email or mobile. Just enter a name as
                            you like.
                        </Text.p>
                        <Box mx={"auto"}>
                            <Field label="Your Name" style={{ width: "100%" }}>
                                <Input
                                    type="text"
                                    style={{ width: "100%" }}
                                    value={value}
                                    onChange={(e) => setValue(e.target.value)}
                                    placeholder="eg. Superman"
                                />
                            </Field>
                            <Button
                                icon="ArrowForward"
                                iconpos="right"
                                onClick={() => {
                                    sessionStorage.setItem("user", value);
                                    sessionStorage.setItem("kind", 1);
                                    window.location.hash = "#/setup";
                                }}
                            >
                                Lets go
                            </Button>
                        </Box>
                        <Text.p style={{ fontStyle: "italic" }}>
                            Already have keys? Log In{" "}
                            <Link
                                title="Click here to login"
                                onClick={() => setPage(0)}
                            >
                                here.
                            </Link>
                        </Text.p>
                    </React.Fragment>
                )}
            </Card>
        </div>
    );
}

export default AuthPage;
