import React, { useEffect, useState } from "react";
import { Box } from "rimble-ui";
import ReactJson from "react-json-view";
import Logs from "./Logs";
function LogsMenu() {
    const [active, setActive] = useState("logs");
    const [Chain, setChain] = useState([]);
    const [logs, setLogs] = useState([]);
    useEffect(() => {
        window.notifier.subscribe((el) => setChain(el));
        window.peer.logger.subscribe((el) => {
            if (el.length > 10)
                setLogs([
                    ...logs,
                    <Logs
                        text={el}
                        type="Discovery"
                        label="#ef7215"
                        key={el}
                    />,
                ]);
        });
        window.minerLog.subscribe((el) => {
            if (el.length > 3)
                setLogs([
                    ...logs,
                    <Logs text={el} type="Miner" label="#46bf14" key={el} />,
                ]);
        });
    }, []);
    return (
        <div className="logs" style={{ borderLeft: "1px solid #eeeeee" }}>
            <div
                className="menu"
                style={{ height: "100vh", overflowY: "hidden" }}
            >
                <Box
                    bg={"#2f3136"}
                    p={2}
                    style={{
                        display: "flex",
                        justifyContent: "space-around",
                        alignItems: "center",
                    }}
                >
                    <span
                        className={`selector ${
                            active === "logs" ? "active" : ""
                        }`}
                        onClick={() => setActive("logs")}
                    >
                        Log
                    </span>
                    <span
                        className={`selector ${
                            active === "blocks" ? "active" : ""
                        }`}
                        onClick={() => setActive("blocks")}
                    >
                        Blockchain
                    </span>
                </Box>
                <Box
                    bg={"#111111"}
                    p={2}
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        height: "-webkit-fill-available",
                        wordBreak: "break-word",
                        overflowY: "scroll",
                        height: "89.9%",
                    }}
                >
                    {active === "logs" && logs.map((el) => el)}
                    {active === "blocks" && (
                        <ReactJson
                            src={Chain}
                            theme="colors"
                            name={"Blockchain"}
                            indentWidth={2}
                            enableClipboard={false}
                            displayDataTypes={false}
                            collapsed={true}
                        />
                    )}
                </Box>
            </div>
        </div>
    );
}

export default LogsMenu;
