import React, { useEffect } from "react";
import { Button, Card, Heading, Text, Box } from "rimble-ui";
function OnBoarding() {
    const kind = sessionStorage["kind"];
    useEffect(() => {
        if (!kind) window.location.hash = "#/auth";
        if (kind === "1") {
            window.user = new window.User(null, null, sessionStorage["user"]);
            sessionStorage.setItem("whoami", window.user.keyHash);
            window.peer = new window.Peer(
                window.user.keyHash,
                window.user.whoami
            );
        }
        if (kind === "0") {
            const file = sessionStorage["file"];
            if (!file) window.location.hash = "#/auth";
            window.user = new window.User(
                JSON.parse(file).publicKey,
                JSON.parse(file).secretKey,
                JSON.parse(file).username
            );
            window.peer = new window.Peer(
                window.user.keyHash,
                window.user.whoami
            );
            if (window.user.keyHash !== JSON.parse(file).keyHash)
                window.location.hash = "#/auth";
            sessionStorage.setItem("whoami", window.user.keyHash);
            window.location.hash = "#/";
        }
    }, []);

    function downloadKEY() {
        const a = document.createElement("a");
        a.href = window.URL.createObjectURL(
            new Blob([JSON.stringify(window.user.userInfo)], {
                type: "meschain",
            })
        );
        a.download = "key.meschain";
        a.click();
        window.URL.revokeObjectURL(a.href);
    }
    return (
        <div className="main__class">
            <Card
                width={"auto"}
                mx={"auto"}
                my={"auto"}
                maxWidth={"420px"}
                bg={"#2c2c2c"}
                color={"white"}
                border={0}
                p={4}
                style={{ boxShadow: "9px 9px 8px rgb(0 0 0)" }}
            >
                {kind === "1" && (
                    <React.Fragment>
                        <Heading>Hang On</Heading>
                        <Text.p>
                            Download your keys and keep them at a safe private
                            place.
                        </Text.p>
                        <Box
                            style={{
                                display: "flex",
                                justifyContent: "center",
                            }}
                        >
                            <Button.Text
                                icon="ArrowForward"
                                iconpos="right"
                                style={{ color: "#ffffff" }}
                                onClick={() => (window.location.hash = "#/")}
                            >
                                Continue
                            </Button.Text>
                            <Button mx={4} onClick={downloadKEY}>
                                Download
                            </Button>
                        </Box>
                        <Text.p style={{ fontStyle: "italic" }}>
                            If you loose this key their is no way of accessing
                            your messages again.
                        </Text.p>
                    </React.Fragment>
                )}
                {kind === "0" && (
                    <React.Fragment>
                        <Heading>Hang On</Heading>
                        <Text.p>
                            Just a moment. We're preparing your chats.
                        </Text.p>
                        <Text.p style={{ fontStyle: "italic" }}>
                            You'll be redirected automatically
                        </Text.p>
                    </React.Fragment>
                )}
            </Card>
        </div>
    );
}

export default OnBoarding;
