import React, { useRef } from "react";
import { Blockie, Box, Text } from "rimble-ui";
const randomColor = () =>
    `#${Math.floor(Math.random() * 16777215).toString(16)}`;
function ChatHistoryItem({ address, lastText, name, handler }) {
    const ref = useRef();
    return (
        <Box
            style={{
                display: "flex",
                borderBottom: "1px solid #ded7d7",
                padding: "10px",
            }}
            onClick={() => {
                handler({
                    address: address,
                    blockie: ref.current.canvas.toDataURL(),
                });
            }}
        >
            <Blockie
                style={{ borderRadius: "50%" }}
                opts={{
                    seed: address,
                    color: randomColor(),
                    bgcolor: randomColor(),
                    spotcolor: randomColor(),
                }}
                ref={ref}
            />
            <span className="d-flex flex-column pl-2">
                <Text fontWeight={"bold"} p={0}>
                    {name.slice(0, 40)}
                </Text>
                <small>{lastText}</small>
            </span>
        </Box>
    );
}

export default ChatHistoryItem;
